<h1 id="thesis-proposals" tabindex="-1"><a class="header-anchor" href="#thesis-proposals" aria-hidden="true">#</a> Thesis Proposals</h1>
<p>On this page you find thesis proposals by me (Peter Larsson-Green). The proposals are just brainstormed ideas and need to be researched more to figure out if they are suitable for thesis work or not (such as checking wether someone has done something similar already). The tools required to carry out the thesis are not available.</p>
<h2 id="laser-pointer-snake" tabindex="-1"><a class="header-anchor" href="#laser-pointer-snake" aria-hidden="true">#</a> Laser Pointer Snake</h2>
<p>Use a 2*2 m² area on a wall as a board for a multiplayer snake game. Point laser pointers (one color for each player) on the screen to instruct the snakes in which direction to move (towards the pointer). Record the board with a camera to find out directions. Use a projector to show how the snakes have moved. Other games can be implemented too, the main idea is to use laser pointers as controllers, catch where they point using a camera, and then visualize the game using a projector.</p>
<p>Required tools:</p>
<ul>
<li>Laser pointers in different colors</li>
<li>Camera to record the board (e.g. smartphone)</li>
<li>Projector to visualize game (or TV screen? Or will the screen reflect the laser beam and hit people's eyes?)</li>
<li>Device to run the game (e.g. computer)</li>
</ul>
<p>Potential research questions:</p>
<ul>
<li>Best way to find the location of the laser pointers on the board?</li>
<li>What resolution is required for the camera to provide a good gaming experience?</li>
<li>What resolution is required for the projector to provide a good gaming experience?</li>
</ul>
<h2 id="real-space-invaders" tabindex="-1"><a class="header-anchor" href="#real-space-invaders" aria-hidden="true">#</a> Real ~Space Invaders</h2>
<p>Use a 2*2 m² area on a wall as a board for a space invaders similar game. Aliens are slowly moved down, and the player should throw a tennis ball/kick a football to hit and destroy the aliens before the reach the ground. Use a projector to visualize the game, and a camera to find where on the board the ball hits the wall.</p>
<p>Required tools:</p>
<ul>
<li>A ball of some kind (tennis ball, football, etc.)</li>
<li>Camera to record the board (e.g. smartphone)</li>
<li>Projector to visualize game</li>
<li>Device to run the game (e.g. computer)</li>
</ul>
<p>Potential research questions:</p>
<ul>
<li>Best way to find the location of the ball on the board when it hit the wall? Calibration needed for different cameras, board sizes and distance between the wall and the camera?</li>
<li>What resolution is required for the camera to provide a good gaming experience?</li>
<li>What resolution is required for the projector to provide a good gaming experience?</li>
</ul>
<h2 id="predict-chanterelles-location" tabindex="-1"><a class="header-anchor" href="#predict-chanterelles-location" aria-hidden="true">#</a> Predict Chanterelles Location</h2>
<p>Mark on a satellite map where chanterelles grow. Use machine learning to train a model to recognize where chanterelles grow.</p>
<p>Required tools:</p>
<ul>
<li>A lot of known locations where chanterelles grow (I don't have any!)</li>
<li>A satellite map (e.g. from Google Maps)</li>
</ul>
<p>Potential research questions:</p>
<ul>
<li>Can a machine learned model be trained to recognize where chanterelles grow just by using a satellite image?</li>
<li>Does the faulty predictions have anything in common that potentially explains why they are faulty?</li>
</ul>
<h2 id="navigating-roads-without-gps-receivers" tabindex="-1"><a class="header-anchor" href="#navigating-roads-without-gps-receivers" aria-hidden="true">#</a> Navigating Roads without GPS Receivers</h2>
<p>GPS receivers are great when navigating from Point A to Point B, but what if they would suddenly stop working? Can the sensors on a smartphone alone provide enough information to navigate you from Point A to Point B, given that you start at Point A, and that the sensor data for the roads has been gathered from before? The thesis is about investigating if sensors on a smartphone (magnetometer, accelerometer, etc.) can be used to navigate you from Point A to Point B.</p>
<p>Required tools:</p>
<ul>
<li>A smartphone</li>
<li>A vehicle (car, bike, etc.) for testing (walking/running might work)</li>
</ul>
<p>Potential research questions:</p>
<ul>
<li>While travelling on a road, will the sensor data from smartphones vary enough from different parts of the road to recognize the same pattern when travelling there again?</li>
<li>Are sensor readings from smartphones while travelling on roads unique enough so sensor readings from a ~30 second ride on a road can identify which road the user is travelling on?</li>
</ul>
